import {Theme} from "./Theme";

type FontPropsType = {
    family?: string,
    weight?: number,
    color?: string,
    lineHeight?: string,
    maxSize: number,
    minSize: number
}
export const font = ({family, weight,color,lineHeight, maxSize, minSize}:FontPropsType) => `
  font-family: ${family || 'Poppins'};
  font-weight: ${weight || 400};
  color: ${color || Theme.colors.mainText};
  line-height: ${lineHeight || 1.2};
  font-size: calc( (100vw - 360px)/(1440 - 360) * (${maxSize} - ${minSize}) + ${minSize}px);
`
